<template>
  <div id="app">
    <mynav></mynav>
    <router-view></router-view>
    <myfooter></myfooter>
  </div>
</template>

<script>
  import nav from "@/components/nav"
  import footer from "@/components/Footer"
  export default {
    name: 'app',
    components: {
      'mynav': nav,
      'myfooter': footer
    },
  }
</script>

<style>
  * {
    margin: 0 0;
    padding: 0 0;
    letter-spacing: 1px;
    box-sizing: border-box;
  }

  body {
    font-size: 14px;
    font-family: "Helvetica Neue", Helvetica, Arial, "Microsoft Yahei", "Hiragino Sans GB", "HeitiSC", "WenQuanYi Micro Hei", sans-serif;
  }

  @font-face {
    font-family: 'iconfont';  /* project id 386639 */
    src: url('//at.alicdn.com/t/font_386639_knnjzi6bjn0cnmi.eot');
    src: url('//at.alicdn.com/t/font_386639_knnjzi6bjn0cnmi.eot?#iefix') format('embedded-opentype'),
    url('//at.alicdn.com/t/font_386639_knnjzi6bjn0cnmi.woff') format('woff'),
    url('//at.alicdn.com/t/font_386639_knnjzi6bjn0cnmi.ttf') format('truetype'),
    url('//at.alicdn.com/t/font_386639_knnjzi6bjn0cnmi.svg#iconfont') format('svg');
  }

  .iconfont {
    font-family: "iconfont" !important;
    font-size: 16px;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    -moz-osx-font-smoothing: grayscale;
  }

</style>



